<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-12-25",
            title: "Promise解决回调地狱",
            items: [
                {
                    title: "Promise",
                    target: "#Promise"
                },
                { divider: true },
                {
                    title: "异步函数",
                    target: "#async"
                },
                { divider: true }
            ],
            pres: 
[
`
    const pm = new Promise((resolve, reject) => {
        setTimeout(function() {
            resolve('OK');
            reject('err'); // resolve先执行，所以此方法不会执行
        }, 500);
    });

    pm.then((value) => {
        // 接受resolve
        console.log(value);
        return value+"!"; // 将值传递给promise实例
    }, (err) => {
        // 接受reject
        console.log(err);
    }).then((value) => {
        // 链式调用
        console.log(value);
        return Promise.reject('err');
    }).catch(function(err) {
        console.log(err);
    }).finally(() => {
        console.log('最终都会执行')
    }); 
    // OK 
    // OK!
    // err
    // 最终都会执行
`,
`
    const pm = Promise.resolve('OK');
    let rpm = pm.then((value)=>{
        // then中的处理函数是异步执行的
        return value;
    });
    console.log(rpm);
    setTimeout(()=>{
        console.log(rpm);
    });

    // Promise {<pending>}
    // Promise {<resolved>: "OK"}
`,
`
    Promise.resolve('Ok').then(function(value) {
        setTimeout(function() {
            console.log('1-> ' + value);
        }, 1);
        return value;
    }).then(function(value) {
        setTimeout(function() {
            console.log('2-> ' + value);
        }, 1)
        return value;
    }).then(function(value) {
        console.log('3-> ' + value);
    });
    // 3-> Ok
    // 1-> Ok
    // 2-> Ok
`,
`
    Promise.resolve('Ok').then(function(value) {
        return new Promise(function(resolve, reject) {
            setTimeout(function() {
                console.log('1-> ' + value);
                resolve(value);
            }, 1);
        });
    }).then(function(value) {
        setTimeout(function() {
            console.log('2-> ' + value);
        }, 1)
        return value;
    }).then(function(value) {
        console.log('3-> ' + value);
    });

    // 1-> Ok
    // 3-> Ok
    // 2-> Ok
`,
`
    const pm1 = Promise.resolve('11');
    const pm2 = Promise.reject('22');
    const pm3 = new Promise(function(resolve, reject) {
        setTimeout(function() {
            resolve('33');
        }, 100)
    });

    Promise.all([pm1, pm2, pm3]).then(function(values) {
        console.log(values);
    }).catch(err => { 
        console.log(err)
    });
    // 22
`,
`
    const pm1 = Promise.resolve('11');
    const pm2 = Promise.reject('22');
    const pm3 = new Promise(function(resolve, reject) {
        setTimeout(function() {
            resolve('33');
        }, 100)
    });

    Promise.race([pm1, pm2, pm3]).then(function(value) {
        console.log(value);
        return '执行完了';
    }).catch(err => { 
        console.log(err)
    });

    // 11
`,
`
    function fn1() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('1秒');
            }, 1000);
        });
    }

    function fn2() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('2秒');
            }, 2000);
        });
    }

    async function fn() {
        const start = performance.now();
        var res1 = await fn1(); // 1秒后执行
        var res2 = await fn2(); // 基于上一个await，这里需要执行另外一个2秒
        const end = performance.now();
        console.log(res1, res2, end - start); // 最后的执行时间肯定是3秒左右
    }
    fn();
`,
`
    function fn1() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('1秒');
            }, 1000);
        });
    }

    function fn2() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('2秒');
            }, 2000);
        });
    }

    async function fn() {
        const start = performance.now();
        let res1 = fn1(); // 立即开始计时
        let res2 = fn2(); // 立即开始计时
        console.log(await res1, await res2);
        const end = performance.now();
        console.log(end - start); // 最后执行时间2秒左右
    }
    fn();
`,
`
    function fn1() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('1秒');
            }, 1000);
        });
    }

    function fn2() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('2秒');
            }, 2000);
        });
    }

    async function fn() {
        const start = performance.now();
        let res = await Promise.all([
            (async () => {
                return await fn1();
            })(),
            (async () => {
                return await fn2();
            })()
        ])
        const end = performance.now();
        console.log(res, end-start);
    }
    fn(); // 最后执行时间2秒左右
`,
`
    function fn1() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('1秒');
            }, 1000);
        });
    }

    function fn2() {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve('2秒');
            }, 2000);
        });
    }

    const fn = async function () {
        return await Promise.all([
            (async () => {
                return await fn1();
            })(),
            (async () => {
                return await fn2();
            })()
        ])
    }
    fn().then(val => {
        console.log(val);
    })
`
]
